import React, { useState, useEffect } from "react";

import { rFetchOrderInfo } from "../../services/api"; //  API  

import "./OrderEnd.css";

const OrderEnd = ({ orderId }) => { //  orderId props 
  //     state
  const [orderInfo, setOrderInfo] = useState({});
  const [isLoading, setIsLoading] = useState(true); 
  const [error, setError] = useState(null); 

  // URL  (order_id) 
  
  const userId = sessionStorage.getItem('userId'); //  userId 
  

  //  useEffect   
  useEffect(() => {
    const fetchOrderInfo = async () => {
      try {
        const { data } = await rFetchOrderInfo(orderId, userId); //  orderId 
        console.log('    (data):', data); //   
        setOrderInfo(data || {});
        console.log(' orderInfo  :', data || {}); //   
      } catch (error) {
        setError("     .");
      } finally {
        setIsLoading(false);
      }
    };
  
    if (orderId) {
      fetchOrderInfo();
    }
  }, [orderId, userId]);

  
  //    
  if (error) {
    return <div className="error-message">{error}</div>;
  }

  return (
    <div className="content_jh">
      {/*   */}
      <div className="filter-header">
        <h2></h2>
      </div>

      {/*   */}
      <div className="complete_top2">
        <ol>
          <li><span>01 </span></li>
          <li><span>02 /</span></li>
          <li className="bold_li"><span>03 </span></li>
        </ol>
      </div>

      {/*    */}
      <div className="complete_msg_div">
        <i
          className="fa-solid fa-bag-shopping fa-2xl"
           style={{
             color: "#F294B2",
             fontSize: "113px",
             position: "relative",
               margin: "20px 0px",
               height: "80px"
         }}
        ></i>
        <p>   .</p>
        <p>.</p>
      </div>

      {/*    */}
      <div className="order_tit">
        <p></p>
      </div>

      <div className="complete_table_div">
        <table className="complete_table">
          <tbody>
            <tr>
              <th></th>
              <td>
                <strong>{orderInfo?.pay_method}</strong>
                {/* <ul>
                  <li> : 164-910017-44504</li>
                  <li> : </li>
                </ul> */}
              </td>
            </tr>
            <tr>
              <th></th>
              <td>{orderInfo?.order_id}</td>
            </tr>
            <tr>
              <th></th>
              <td>{orderInfo?.order_date}</td>
            </tr>
            <tr>
              <th></th>
              <td>{orderInfo?.product_summary}</td>
            </tr>
            <tr>
              <th></th>
              <td>{orderInfo?.orderer_name}</td>
            </tr>
            <tr>
              <th></th>
              <td>
              <strong>:</strong> {orderInfo?.receiver_name} 
    <br />
    <strong>:</strong> {orderInfo?.receiver_address}
    <br />
    <strong> :</strong> {orderInfo?.receiver_phone} 
  </td>
            </tr>
            {/* <tr>
              <th> </th>
              <td><strong className="big_price">{orderInfo?.total_price?.toLocaleString()}</strong></td>
            </tr>
            <tr>
              <th></th>
              <td>0</td>
            </tr> */}
            {/* <tr>
  <th> </th>
  <td> : (-){orderInfo?.mileage_used?.toLocaleString()}</td> 
</tr> */}
<tr>
  <th> </th>
  <td>{new Intl.NumberFormat().format((orderInfo?.total_price || 0) - (orderInfo?.mileage_used || 0))}</td>
</tr>
          </tbody>
        </table>
      </div>

      {/*   */}
      <div className="complete_btn_div">
        <button
          type="button"
          className="complete_btn"
          onClick={() => (window.location.href = "#/")}
        >
          
        </button>
      </div>
    </div>
  );
};

export default OrderEnd;