import { useState, useEffect } from 'react';
import { checkPw } from '../../services/api';
import { useNavigate } from 'react-router-dom'
import styles from './memModify1.module.css';

const MemModify1 = () => {
  const [userId, setUserId] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  //    
  useEffect(() => {
    const storedUserId = sessionStorage.getItem('userId');
    if (storedUserId) {
      setUserId(storedUserId);
    } else {
      alert(' .');
      navigate('/login');
    }
  }, [navigate]);

  //     
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await checkPw(userId, password);
      navigate('/memModify2');
    } catch (error) {
      if (error.response && error.response.status === 401) {
        alert('  .');
      } else {
        alert('  .');
      }
    }
  };

  return (
    <div className={styles.infoChangeContainer}>
      <h2> </h2>
      <p>         .</p>
      <form onSubmit={handleSubmit}>
        <div className={styles.inputContainer}>
          <span> <strong className={styles.userId}>{userId}</strong></span><br/>

          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder=""
            required
          />
        </div>
        <div className={styles.modifyButtons}>
          <button type="button" onClick={() => navigate('/')}></button>
          <button type="submit"></button>
        </div>
      </form>
    </div>
  );
}

export default MemModify1;