import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { selectBoardAnswer, updateBoardCount } from '../../services/api'; // API  
import PaginationControls from '../pagination/PaginationControls';

// import "./NoticePage.css";
import styles from "./itemInquery.module.css";

function NoticePage(props) {
    const navigate = useNavigate(); //    navigate 

    const [boards, setBoards] = useState([]); //  
    const [currentPage, setCurrentPage] = useState(1); //  
    const [selectedCode, setSelectedCode] = useState('nt'); //  
    const [selectedId, setSelectedId] = useState(null); //  ID
    const [selectComments, setSelectedComment] = useState(null); //  
    const [selectedContents, setSelectedContents] = useState(null); //  
    const [startDate, setStartDate] = useState(init(0));
    const [endDate, setEndDate] = useState(init(1));

    // 
    const itemsPerPage = 10; //    
    const totalPages = Math.ceil(boards.length / itemsPerPage); //   

    //    
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = boards.slice(indexOfFirstItem, indexOfLastItem);
    
    useEffect(() => {        
        searchBoards();
    }, []);

    //    
    function init(t){//t  0 , 1 
        const start = new Date();

        if(!t){
            start.setDate(start.getDate() - 30);
        }

        return start.toISOString().split('T')[0];
    }

    const handleRowClick = (id, title, contents, code, comment, reg_id) => {
        //  +1
        updateBoardCount(id)
        .then(() => {
            console.log(" +1 ");
        })
        .catch((error) => {
            console.error(" +1 ", error);
        });
        
        setSelectedId(id);
        setSelectedContents(contents);
        setSelectedComment(comment);
        setSelectedCode(code);

        navigate(`/notice/itemQueryDetail`, {
            state: { id, title, contents, comment, code, previousPage: '/notice/notice_page', reg_id}
        });
    };

    const searchBoards = (event) => {
        if (event) event.preventDefault();

        if(startDate > endDate){
            alert("    .");
            return;
        }

        const frmData = new FormData(document.myFrm);
        frmData.append("code", selectedCode);
        const myData = Object.fromEntries(frmData);

        selectBoardAnswer(myData)
            .then((res) => {
                setBoards(res.data);
                setCurrentPage(1);
            })
            .catch((error) => console.error('Error fetching boards:', error));
    };

    const handlePageChange = (page) => {
        if (page >= 1 && page <= totalPages) {
            setCurrentPage(page);
        }
    };

    //   
    const handleDateButtonClick = (days, event) => {
        if (event) event.preventDefault();
        
        const today = new Date();
        setEndDate(today.toISOString().split('T')[0]);

        if (days === 7 || days === 15) {
            today.setDate(today.getDate() - days);
        } else if (days === 30) {
            today.setMonth(today.getMonth() - 1);
        } else if (days === 90) {
            today.setMonth(today.getMonth() - 3);
        } else if (days === 365) {
            today.setFullYear(today.getFullYear() - 1);
        }
        setStartDate(today.toISOString().split('T')[0]);
    };
    
    return (
        <div className={styles.inquiry_section}>
            <div className={styles.filter_header}>
                <h2></h2>
                <span></span>
            </div>
            <form name='myFrm'>
                <div className={styles.mypage_date_check}>
                    <div className={styles.mypage_date_period}>
                        <span className={styles.mypage_period_text}></span>
                        <button data-value="0" onClick={(e) => handleDateButtonClick(0, e)}></button>
                        <button data-value="7" onClick={(e) => handleDateButtonClick(7, e)}>7</button>
                        <button data-value="15" onClick={(e) => handleDateButtonClick(15, e)}>15</button>
                        <button data-value="30" onClick={(e) => handleDateButtonClick(30, e)}>1</button>
                        <button data-value="90" onClick={(e) => handleDateButtonClick(90, e)}>3</button>
                        <button data-value="365" onClick={(e) => handleDateButtonClick(365, e)}>1</button>
                    </div>
                    <div className={styles.mypage_date_box}>
                        <input name="start_date" type="date" className={styles.mypage_first_date} onChange={(e) => setStartDate(e.target.value)} value={startDate}/>&nbsp;-&nbsp;
                        <input name="end_date" type="date" className={styles.mypage_last_date} onChange={(e) => setEndDate(e.target.value)} value={endDate}/>
                    </div>
                    <div className={styles.mypage_date_search}>
                        <span className={styles.mypage_date_search_btn} onClick={searchBoards}></span>
                    </div>
                </div>
            </form>

            <div className={styles.table_container}>
                <table id="boardTable">
                    <thead>
                        <tr>                            
                            <th style={{ width: "15%" }}></th>
                            <th style={{ width: "30%" }}></th>
                            <th style={{ width: "8%" }}></th>
                            <th style={{ width: "10%" }}></th>
                            <th style={{ width: "20%" }}></th>
                        </tr>
                    </thead>
                    <tbody id="boardBody">
                        {currentItems.map((item) => (
                            <tr key={item.id} onClick={() => handleRowClick(item.id, item.title, item.contents, item.code, item.comment, item.reg_id)}>
                                <td>{item.id}</td>
                                <td>{item.title}</td>
                                <td>{item.read_no}</td>
                                <td>{item.reg_id}</td>
                                <td>{item.reg_date}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>

            <PaginationControls
                currentPage={currentPage}
                totalPages={totalPages}
                onPageChange={handlePageChange}
            />
        </div>
    );
}

export default NoticePage;
