import { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { insertBoardContentImage, insertBoardContent } from '../../services/api';

import styles from './ItemWrite.module.css';

function ItemWrite(props) {
    const location = useLocation();
    const { code, previousPage } = location.state || {};
    const [file, setFile] = useState(null); //   
    const [title, setTitle] = useState(''); // title 
    const [contents, setContents] = useState(''); // contents 
    const userName = sessionStorage.getItem('userName');
    const userId = sessionStorage.getItem('userId');

    useEffect(() => {
        
    }, []);

    function commentSubmit(event) {
        event.preventDefault();
        
        if (!title.trim()) {
            alert(" !");
            return;
        }
        
        if (!contents.trim()) {
            alert(" !");
            return;
        }
        
        const frmDataTemp = new FormData(document.myFrm);
        frmDataTemp.append("code", code);
        frmDataTemp.append("userId", userId);
        const frmData = Object.fromEntries(frmDataTemp);
        
        insertBoardContent(frmData)
        .then((res) => {
            alert(" .");
            // setFile(null);  //   
            window.location.href = previousPage;
        })
        .catch((error) => {
            console.error("Error uploading file:", error);
            alert("  !");
        });
    }

    //   
    const handleFileChange = (e) => {
        const selectedFile = e.target.files[0];
        setFile(selectedFile);
    };

    // title   
    const handleTitleChange = (e) => {
        setTitle(e.target.value); // title  
    };

    // contents   
    const handleContentsChange = (e) => {
        setContents(e.target.value); // contents  
    };

    //   
    const submitGo = (e) => {
        e.preventDefault(); //  
        
        if (!title.trim()) {
            alert(" !");
            return;
        }
        
        if (!contents.trim()) {
            alert(" !");
            return;
        }
        
        if (!file) {
            commentSubmit();
            return;
        }

        const frmData = new FormData();
        frmData.append('upfile', file);  // 'upfile' key   

        //    (: title, contents, code, userId )
        frmData.append('title', title); //  title  
        frmData.append('contents', contents); //  contents  
        frmData.append('code', code);
        frmData.append('userId', userId);

        for (let [key, value] of frmData.entries()) {
            console.log(key + ': ' + value);  //    
        }

        //   API 
        insertBoardContentImage(frmData)
        .then((res) => {
            alert("  !");
            // setFile(null);  //   
            window.location.href = previousPage;
        })
        .catch((error) => {
            console.error("Error uploading file:", error);
            alert("  !");
        });
    };

    return (
        <div className={styles.wrapper}>
            <div className={styles.filter_header}>
                <h2></h2>
                <span>{code == "rv" ? "" : (code == "pr" ? "" : (code == "pq" ? "" : (code == "fq"? "FAQ" : (code == "nt" ? "" : "1:1"))))}</span>
            </div>
            <form name='myFrm' onSubmit={submitGo}>
                <table border="" className={styles.write_table}>
                    <tbody>
                        <tr>
                            <th></th>
                            <td>{userName}</td>
                        </tr>
                        <tr>
                            <th></th>
                            <td>
                                <label className={styles.title_label} htmlFor="name_title">
                                    <input 
                                        type="text" 
                                        placeholder=" ." 
                                        name="title" 
                                        value={title} //   
                                        onChange={handleTitleChange} //     
                                    />
                                </label>
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td>
                                <textarea 
                                    name="contents" 
                                    placeholder="" 
                                    className={styles.textfield}  
                                    style={{ width: '100%', height: '150px' }} 
                                    value={contents} //   
                                    onChange={handleContentsChange} //     
                                />
                            </td>
                        </tr>
                        {code === 'pr' ? (
                            <tr>
                                <th></th>
                                <td>
                                    <input className={styles.file_upload} type="file" name="upfile" onChange={handleFileChange} />
                                </td>
                            </tr>
                        ):( <></> )}
                    </tbody>
                </table>

                <div className={styles.btn_container}>
                    <button type="button" className={styles.btn_before} onClick={() => window.location.href = previousPage}>
                        <strong className={styles.itone}></strong>
                    </button>
                    {code === 'pr' ? (
                        <input className={styles.btn_write} style={{margin:"5px"}} type="submit" value="" />
                    ):( <button className={styles.btn_write} onClick={commentSubmit}><strong className={styles.ittwo}></strong></button> )}
                </div>
            </form>
        </div>
    );
}

export default ItemWrite;
