import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

import styles from './Cart.module.css';

import { rFetchCartItems, dDeleteCartItem, uSubmitOrder } from '../../services/api';

import Header from "../Header";
import Footer from "../Footer";

const Cart = () => {
  const userId = sessionStorage.getItem('userId');
  const navigate = useNavigate();
  const [cartItems, setCartItems] = useState([]);
  const [totalPrice, setTotalPrice] = useState(0);
  const [isAllChecked, setIsAllChecked] = useState(true);
  
  useEffect(() => {
    //      
    if (!userId) {
      alert(" .   .");
      navigate("/login");
    }

    const fetchCartItems = async () => {
      try {
        const response = await rFetchCartItems(userId);
        if (response.status !== 200) throw new Error("HTTP  : " + response.status);
        const data = response.data;
        // console.log(" ",data);
        
        if (!data || !data.cartItems) throw new Error("cartItems .");
  
        //    
        setCartItems(data.cartItems.map((item) => ({ ...item, checked: true })));
  
        //     
        data.cartItems.forEach((item, index) => {
          console.log(`   ${index + 1} :`, item);
          console.log(`   JSON.stringify:`, JSON.stringify(item.options, null, 2)); //  1
          console.dir(item.options, { depth: null }); //  2
          if (item.options) {
            item.options.forEach((option, idx) => {
              console.log(`  ${idx + 1}:`, option); //  3
            });
          } else {
            console.log('    .');
          }
        });
        //     
  
      } catch (error) {
        console.error("     :", error);
      }
    };
  
    fetchCartItems();
  }, []);
  
  useEffect(() => {
    const total = cartItems
      .filter((item) => item.checked)
      .reduce((acc, item) => acc + item.final_price * item.quantity, 0);
  
    setTotalPrice(total);
  }, [cartItems]);

  const handleAllCheck = (e) => {
    const checked = e.target.checked;
    setIsAllChecked(checked);
    setCartItems((prevItems) =>
      prevItems.map((item) => ({
        ...item,
        checked,
      }))
    );
  };

  const handleCheckboxChange = (id) => {
    setCartItems((prevItems) =>
      prevItems.map((item) =>
        item.cart_id === id ? { ...item, checked: !item.checked } : item
      )
    );
  };

  const handleDeleteSelected = async () => {
    const selectedItems = cartItems.filter((item) => item.checked);
    const selectedCount = selectedItems.length;
  
    if (selectedCount === 0) {
      alert("  .");
      return;
    }
  
    const confirmDelete = window.confirm(
      ` ${selectedCount}   ?`
    );
  
    if (confirmDelete) {
      try {
        //     
        await Promise.all(
          selectedItems.map((item) => dDeleteCartItem(item.cart_id))
        );
  
        //   
        setCartItems((prevItems) => prevItems.filter((item) => !item.checked));
      } catch (error) {
        console.error("     :", error);
        alert("   .");
      }
    }
  };

  

  const handleOrderProducts = async () => {
    try {
      const userId = sessionStorage.getItem('userId');
      if (!userId) {
        alert(" .");
        window.location.href = '#/login';
        return;
      }
  
      const selectedItems = cartItems.filter((item) => item.checked);
      if (!Array.isArray(selectedItems) || selectedItems.length === 0) { 
        alert("  .  .");
        return;
      }
  
      const orderData = selectedItems.map((item, index) => {
        if (!item.product_id) {
          console.warn(` product_id . (index: ${index}, item: ${JSON.stringify(item)})`);
        }
        return {
          product_id: item.product_id ?? 0, 
          quantity: item.quantity ?? 1, //  quantity  1  (0  1)
          price: item.price ?? 0 //  item.price  (item.final_price * item.quantity Order )
        };
      });
  
     
  
      //  sessionStorage   
      sessionStorage.setItem('orderData', JSON.stringify(orderData)); 
  
      //  Order  
      window.location.href = '#/order'; 
    } catch (error) {
      console.error('      :', error);
      alert('    .');
    }
  };

 

  return (
    <div className={styles.content_jh}>
      <div className={styles.filterHeader}>
        <h2></h2>
      </div>
  
      <div className={styles.order_top2}>
        <ol>
          <li className={styles.bold_li}>
            <span>01 </span>
          </li>
          <li>
            <span>02 /</span>
          </li>
          <li>
            <span>03 </span>
          </li>
        </ol>
      </div>
  
      {cartItems.length > 0 ? (
        <div className={styles.table_div}>
          <table>
            <tbody>
              <tr>
                <th>
                  <input 
                    type="checkbox" 
                    id="tot_chk" 
                    checked={isAllChecked} 
                    onChange={handleAllCheck} 
                  />
                </th>
                <th>/ </th>
                <th></th>
                <th></th>
                <th>/</th>
                <th></th>
                <th></th>
              </tr>
  
              {cartItems.map((item) => (

                
                <tr key={item.cart_id}>
                  <td>
                    <input 
                      type="checkbox" 
                      checked={item.checked} 
                      onChange={() => handleCheckboxChange(item.cart_id)} 
                    />
                  </td>
                  <td className={styles.name_td}>
                  <img 
  src={`/content/img/main/main_product${String(item.product_id).padStart(2, '0')}.jpg`} 
  alt={item.product_name} 
  style={{ width: "40px", height: "32px" }} 
/>
                    {item.product_name}

                   {/*     */}
{item.options && (
  <div className={styles.option_info}>
    {(() => {
      try {
        //     
        const parsedOptions = typeof item.options === 'string'
          ? JSON.parse(item.options) // JSON   
          : item.options; //     

        //      
        if (parsedOptions && typeof parsedOptions === 'object') {
          return Object.values(parsedOptions).join(' / '); //   
        } else {
          return "  .";
        }
      } catch (error) {
        console.error("  :", error);
        return "    .";
      }
    })()}
  </div>
)}
        {/*  */}
                  </td>
                  <td>{item.quantity}</td>
                  <td>{item.final_price.toLocaleString()}</td>
                  <td>{item.discount}%<br />(-{(item.price * (item.discount / 100)).toLocaleString()})</td>
                  <td>{(item.final_price * item.quantity).toLocaleString()}</td>
                  <td></td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        <div
          id="empty_cart_message"
          style={{ textAlign: "center", fontSize: "18px" }}
        >
          <br />
          <br />
          <br />
             .
        </div>
      )}
  
      <div className={styles.btn_div}>
        <div className={styles.small_left_div}>
          <div className={styles.delete_btn_div}>
            <button 
              type="button" 
              id="delete_btn" 
              onClick={handleDeleteSelected}
            >
                
            </button>
          </div>
        </div>
  
        <div className={styles.small_right_div}>
          <button
            type="button"
            id="product_btn"
            onClick={() =>  window.location.href = '#/goods/goodsList'}
          >
             
          </button>
        </div>
      </div>
  
      <div className={styles.price_div}>
        <div className={styles.price_sum_right}>
          <div className={styles.left_div}>
            <div></div>
            <div className={styles.left_price}>{totalPrice.toLocaleString()}</div>
          </div>
          <div className={styles.center_div}>
            <div></div>
            <div className={styles.center_price}>0</div>
          </div>
          <div className={styles.right_div}>
            <div></div>
            <div className={styles.right_price}>{totalPrice.toLocaleString()}</div>
          </div>
        </div>
      </div>
  
      <div className={styles.order_bottom_div}>
        <div className={styles.order_btn_div}>
          <button
            type="button"
            id="all_buy_btn"
            onClick={handleOrderProducts}
          >
             
          </button>
        </div>
      </div>
    </div>
  );
};

export default Cart;