import { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";

//  css, js
import '../css/reset.css';
import '../css/fonts.css';
import '../css/default.css';
import '../js/default.js';
// import '../js/sub.js';
import 'swiper/css';
import 'swiper/css/thumbs';
import 'swiper/css/free-mode';

function Header() {
  const [isLoggedIn, setIsLoggedIn] = useState(!!sessionStorage.getItem('userName'));
  const [username, setUsername] = useState(sessionStorage.getItem('userName') || '');
  const navigate = useNavigate();

  // storage     
  useEffect(() => {
    const handleStorageChange = () => {
      const storedUserName = sessionStorage.getItem('userName');
      setIsLoggedIn(!!storedUserName);
      setUsername(storedUserName || '');
    };

    window.addEventListener('storage', handleStorageChange);

    return () => {
      window.removeEventListener('storage', handleStorageChange);
    };
  }, []);

  const env = process.env.REACT_APP_BACK_URL;
  const handleLogout = () => {
    sessionStorage.clear();
    setIsLoggedIn(false);
    setUsername('');

    // storage   
    window.dispatchEvent(new Event('storage'));

    alert(' .');
    //navigate('/'); //   
    window.location.href = '#/';
  };

  return (
    <header className="on">
      <div className="header-wrap">
        <ul className="head-right">
          <li>
            {isLoggedIn ? (
              <span id="welcomeMessage">{username}, .</span>
            ) : (
              <Link to="/joinMethod" id="join">
                
              </Link>
            )}
          </li>
          <li>
            {isLoggedIn ? (
              <Link to="#" id="logout" onClick={handleLogout}>
                
              </Link>
            ) : (
              <Link to="/login" id="loginYn">
                
              </Link>
            )}
          </li>
        </ul>
        <div className="gnb-wrap">
          <Link to="/" className="header-logo">
          {/*    */}
          <img src={`${process.env.PUBLIC_URL}/content/img/main/logo.png`} alt="ILOKE logo" />
          </Link>
          <div className="gnb">
            <ul>
              {/* <li>
                <Link to="/brand/intro"></Link>
                <ol>
                  <li>
                    <Link to="/brand/intro"> </Link>
                  </li>
                  <li>
                    <Link to="/brand/showRoomInfo"> </Link>
                  </li>
                </ol>
              </li> */}
              <li>
                <Link to="/goods/goodsList"></Link>
                <ol>
                  <li>
                    <Link to="/goods/goodsList"></Link>
                  </li>
                  <li>
                    <Link to="/goods/goodsList"></Link>
                  </li>
                  <li>
                    <Link to="/goods/goodsList"></Link>
                  </li>
                  <li>
                    <Link to="/goods/goodsList"></Link>
                  </li>
                  <li>
                    <Link to="/goods/goodsList"></Link>
                  </li>
                  <li>
                    <Link to="/goods/goodsList"></Link>
                  </li>
                </ol>
              </li>
              {/* <li>
                <Link to="/promotion/online_mall"></Link>
                <ol>
                  <li>
                    <Link to="/promotion/online_mall"> </Link>
                  </li>
                  <li>
                    <Link to="/promotion/offline_showroom"> </Link>
                  </li>
                </ol>
              </li> */}
              <li>
                <Link to="/notice/customer_review"></Link>
                <ol>
                  <li>
                    <Link to="/notice/customer_review"></Link>
                  </li>
                  <li>
                    <Link to="/notice/photoReview"></Link>
                  </li>
                </ol>
              </li>
              <li>
                <Link to="/notice/notice_page"></Link>
                <ol>
                  <li>
                    <Link to="/notice/notice_page"></Link>
                  </li>
                  <li>
                    <Link to="/notice/itemInquery"></Link>
                  </li>
                  <li>
                    <Link to="/notice/faq">FAQ</Link>
                  </li>
                  <li>
                    <Link to="/notice/one_inquery">1:1</Link>
                  </li>
                </ol>
              </li>
            </ul>
          </div>
          <div className="gnb-side">
            <ul>
              <li>
                {/* <Link to="/search" className="nav-search">
                  <i className="fa-solid fa-magnifying-glass"></i>
                </Link> */}
              </li>
              <li className="shop">
                <Link to="/cart">
                  <i className="fa-solid fa-cart-shopping"></i>
                  <span>0</span>
                </Link>
              </li>
              <li>
                {isLoggedIn ? (
                  <Link to="/goods/myPage" className="nav-user">
                    <i className="fa-solid fa-user-large"></i>
                  </Link>
                ) : (
                  <Link to="/login" id="loginYn">
                    <i className="fa-solid fa-user-large"></i>
                  </Link>
                )}
              </li>
              <li>
                <a href="#" className="mbl_btn">
                  <span></span>
                  <span></span>
                  <span></span>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </header>
  );
}

export default Header;
