import { useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { findPwReset } from '../../services/api';
import InputForm from './component/inputForm';
import styles from './find_pw_reset.module.css';


const FindPwReset = ({ userId }) => {
  //const { userId } = useParams(); // URL  userId 
  const [userPw, setUserPw] = useState('');
  const [confirmPw, setConfirmPw] = useState('');
  const [errorMsg, setErrorMsg] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async () => {
    //    ( /, ,   2   10 )
    const pwRegex = /^(?!.*\s)(?=.*[a-zA-Z])(?=.*\d|.*[!@#$%^&*])[a-zA-Z\d!@#$%^&*]{10,}$/; //  /, ,   2   10 

    if (!pwRegex.test(userPw)) {
      setErrorMsg('  /, ,   2   10  .');
      return;
    }

    if (userPw !== confirmPw) {
      setErrorMsg('  .');
      return;
    }

    try {
      //    
      const response = await findPwReset(userId, userPw);
      if (response.data.success) {
        alert('  .');
        navigate('/findPwComplete'); //     
      } else {
        setErrorMsg(response.data.message || '  .');
      }
    } catch (error) {
      console.error('  :', error.response?.data?.message || ' ');
      setErrorMsg(error.response?.data?.message || ' ');
    }
  };

  const fields = [
    { type: 'password', id: 'pw_input', placeholder: '  ', value: userPw, onChange: (e) => setUserPw(e.target.value), required: true },
    { type: 'password', id: 'pw_input_chk', placeholder: '  ', value: confirmPw, onChange: (e) => setConfirmPw(e.target.value), required: true },
  ];

  return (
    <section>
      <div className={styles.resetContainer}>
        <div className={styles.resetTitle}> </div>

        <div className={styles.resetPwMsg}>
          <i id="mypage_i" className={`fa-regular fa-user ${styles.mypageIcon}`} style={{ color: '#F294B2' }}></i>
          <p>   .</p>
        </div>

        <InputForm
          fields={fields}
          errorMessage={errorMsg}
          buttonText=""
          onSubmit={handleSubmit}
        />
      </div>
    </section>
  );
};

export default FindPwReset;