import { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { memLogin } from '../../services/api';
import styles from './login.module.scss';

const Login = () => {
  const [id, setPid] = useState('');
  const [pw, setPassword] = useState('');
  const [pname, setPname] = useState('');
  const [orderNo, setOrderNo] = useState('');
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault();

    try {
      const response = await memLogin(id, pw);

      if (response.data.success) {
        const userName = response.data.user.name;

        //     
        sessionStorage.setItem('userId', id);
        sessionStorage.setItem('userName', userName);
        sessionStorage.setItem('auth', 'true');

        // storage   
        window.dispatchEvent(new Event('storage'));

        alert(`${userName}, !`);

        navigate('/home'); //   

        //window.location.reload();

      } else {
        alert(response.data.message || ' .');
      }
    } catch (error) {
       if (error.response) {
      // 403   
      if (error.response.status === 403) {
        alert(' .   .');
      } else if (error.response.status === 401) {
        alert('   .');
      } else {
        alert('  .   .');
      }
    } else {
      alert('  .   .');
    }
  }
};

  return (
    <section className={styles.loginSection}>
      <div className={styles.loginContainer}>
        <div className={styles.loginTitle}> </div>
        <form id="login-form" onSubmit={handleLogin}>
          <input
            type="text"
            className={styles.inputLogin}
            id="pid"
            placeholder=""
            required
            value={id}
            onChange={(e) => setPid(e.target.value)}
          />
          <input
            type="password"
            id="password"
            placeholder=""
            required
            value={pw}
            onChange={(e) => setPassword(e.target.value)}
          />
          <input type="submit" className={styles.btnLogin} value="" />
        </form>

        <div className={styles.linkButtons}>
          <Link to="/joinMethod" className={`${styles.linkButton} ${styles.active}`}>
            
          </Link>
          <Link to="/findId" className={styles.linkButton}>
             
          </Link>
          <Link to="/findPw" className={styles.linkButton}>
             
          </Link>
        </div>

        <p id="result"></p>
        <hr />

        <div className={styles.snsLoginSection}>
          <div className={styles.snsLoginTitle}>SNS  </div>
          <button className={`${styles.snsButton} ${styles.payco}`}>PAYCO  </button>
          <button className={`${styles.snsButton} ${styles.naver}`}>  </button>
          <button className={`${styles.snsButton} ${styles.kakao}`}>  </button>
        </div>

        <hr />
      {/*
        <div className="non-member-section">
          <div className="non-member-title">  </div>
          <input
            type="text"
            className="input-login"
            name="pname"
            placeholder=""
            required
            value={pname}
            onChange={(e) => setPname(e.target.value)}
          />
          <input
            type="text"
            className="input-login"
            name="orderNo"
            placeholder=""
            required
            value={orderNo}
            onChange={(e) => setOrderNo(e.target.value)}
          />
          <button type="button" className="btn-search" onClick={handleOrderSearch}>
            
          </button>
          <p className="non-member-info">
                   .
          </p>
        </div>
        */}
      </div>
    </section>
  );
};

export default Login;