import { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { findPw } from '../../services/api';
import InputForm from './component/inputForm';
import styles from './find_pw.module.css';


const FindPw = () => {
  const [userName, setUserName] = useState('')
  const [userId, setUserId] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const navigate = useNavigate();

  const handleNextClick = async () => {
    try {
      const response = await findPw(userId, userName);
      navigate(`/findPwReset/${userId}`); // find_pw_reset.js 
    } catch (error) {
      setErrorMessage(error.response?.data?.message || ' ');
    }
  };

  const fields = [
    { type: 'text', id: 'name_input', placeholder: ' ', value: userName, onChange: (e) => setUserName(e.target.value), required: true },
    { type: 'text', id: 'id_input', placeholder: ' ', value: userId, onChange: (e) => setUserId(e.target.value), required: true },
  ];

  return (
    <section>
      <div className={styles.findPwContainer}>
        <div className={styles.findPwTitle}> </div>

        <div className={styles.findMsgDiv}>
          <i id="mypage_i" className={`fa-regular fa-user ${styles.mypageIcon}`} style={{ color: '#F294B2' }}></i>
          <p>    .</p>
        </div>

        <InputForm
          fields={fields}
          errorMessage={errorMessage}
          buttonText=""
          onSubmit={handleNextClick}
        />

          <p>
             ?{' '}
            <Link to="/findId"> </Link>
          </p>
        </div>        
    </section>
  );
};

export default FindPw;