import { useState } from "react";
import { memJoin, checkIdDup, checkNickDup } from "../../services/api";
import { useNavigate } from "react-router-dom";
import { Link } from 'react-router-dom';
import styles from './join.module.css'

// ksh

function Join(){
    const navigate = useNavigate();

    const [formData, setFormData] = useState({
        id: "",
        pw: "",
        pw2: "",
        name: "",
        nick: "",
        email: "",
        phone_num: "",
        addr: "",
        birth_date: "",
      });

      const [idMsg, setIdMsg] = useState("");                     //   
      const [idDuplicate, setIdDuplicate] = useState(false);      //  
      const [idValid, setIdValid] = useState(false);              //   
      const [pwMsg, setPwMsg] = useState("");                     //   
      const [pwValid, setPwValid] = useState(false);              //   
      const [pwMatchMsg, setPwMatchMsg] = useState("");          //    
      const [nameValid, setNameValid] = useState(false);          //   
      const [nickDuplicate, setNickDuplicate] = useState(false);  //   
      const [nickMsg, setNickMsg] = useState("");                 //   
      const [emailValid, setEmailValid] = useState(false); //   
      const [emailMsg, setEmailMsg] = useState(""); //   
      const [phoneNumValid, setPhoneNumValid] = useState(false);  //   
      const [addrValid, setAddrValid] = useState(false);          //   
    
      //    
      const validId = (id) => {
        const idRegex = /^(?=.*[a-zA-Z])(?=.*\d)[a-zA-Z\d]{4,12}$/; // ,, ,  4~12 
        if (!idRegex.test(id)) {
          setIdMsg("      4  12  .");
          setIdValid(false);
          return false;
        }
        setIdMsg("  .");
        setIdValid(true);
        return true;
      };
    

      //    
      const checkIdDuplicate = async (id) => {
        if (!id) {
          setIdMsg("");
          setIdDuplicate(false);
          return;
        }

        //       
        if (!validId(id)) {
          return;
        }
      
        try {
          const response = await checkIdDup(id);
          if (response.data.isDuplicate) {
            setIdMsg("   .");
            setIdDuplicate(true);
          } else {
            setIdMsg("  .");
            setIdDuplicate(false);
          }
        } catch (error) {
          setIdMsg("     .");
          setIdDuplicate(true);
        }
      };

       //    
        const checkPwValid = (password) => {
          const pwPattern = /^(?!.*\s)(?=.*[a-zA-Z])(?=.*\d|.*[!@#$%^&*])[a-zA-Z\d!@#$%^&*]{10,}$/; //  /, ,   2   10 
          if (!pwPattern.test(password)) {
              setPwMsg("  /, ,   2   10  .");
              setPwValid(false);
              return false;
          }
          setPwMsg(" .");
          setPwValid(true);
          return true;
        };

        const checkPwMatch = (password, confirmPassword) => {
          if (!password && !confirmPassword) {
            setPwMatchMsg(""); //      
            return false;
          }
          if (password !== confirmPassword) {
              setPwMatchMsg("  .");
              return false;
          }
  
          setPwMatchMsg(" .");
          return true;
        };

        //    
        const validName = (name) => {
          const nameRegex = /^[-]{2,6}$/;  //  2~6
          if (!nameRegex.test(name)) {
            setNameValid(false);
            return false;
          }
          setNameValid(true);
          return true;
        };

        const validNick = (nick) => {
          if (!nick) return true;

          const nickRegex = /^[-A-Za-z0-9]{2,10}$/; //   
          if (!nickRegex.test(nick)) {
              setNickMsg(" , ,   2  10  .");
              setNickDuplicate(true); //    
              return false;
          }
          return true;
      };
      
        //    
        const checkNickDuplicate = async (nick) => {
          if (!nick) { 
            setNickMsg("");  //     
            setNickDuplicate(false); //     
            return;  //      
          }

          if (!validNick(nick)) {
            return;
        }
        
          try {
            const response = await checkNickDup(nick);
            setNickDuplicate(response.data.isDuplicate);
            setNickMsg(response.data.isDuplicate ? "   ." : "  .");
          } catch (error) {
            setNickDuplicate(true);  //    
          }
        };

        //    
        const validEmail = (email) => {
          const emailRegex = /^[a-zA-Z0-9._%+-]+@[a-zA-Z0-9-]+(\.[a-zA-Z0-9-]+)*\.[a-zA-Z]{2,}$/;
          if (!emailRegex.test(email)) {
            setEmailMsg("   .");
            setEmailValid(false);
            return false;
          }
          setEmailMsg("  .");
          setEmailValid(true);
          return true;
        };

         //    
        const validPhoneNum = (phoneNum) => {
          const phoneRegex = /^\d{3}-\d{4}-\d{4}$/; // xxx-xxxx-xxxx 
          if (!phoneRegex.test(phoneNum)) {
              setPhoneNumValid(false);
              return false;
          }
          setPhoneNumValid(true);
          return true;
        };


        //    
        const validAddr = (addr) => {
          const addrRegex = /^[A-Za-z0-9-\s-]{8,}$/; // , , , ,  '-' ,  8 
          if (!addrRegex.test(addr)) {
              setAddrValid(false);
              return false;
          }
          setAddrValid(true);
          return true;
        };

        const handleChange = (e) => {
          const { name, value } = e.target;
  
          setFormData({ ...formData, [name]: value });

        if (name === "pw") {
            checkPwValid(value);
            checkPwMatch(value, formData.pw2);
        } else if (name === "pw2") {
            checkPwMatch(formData.pw, value);
        } else if (name === "id") {
            checkIdDuplicate(value);
        } else if (name === "name") {
            validName(value);
        } else if (name === "nick") {
            checkNickDuplicate(value);
        } else if (name === "email") {
            validEmail(value);
        } else if (name === "emailDomain") {
            const updatedEmail = `${formData.email.split("@")[0]}@${value}`;
            setFormData({ ...formData, email: updatedEmail });
            validEmail(updatedEmail);
        } else if (name === "phone_num") {
            validPhoneNum(value);
        } else if (name === "addr") {
            validAddr(value);
        }
    };
    
        const handleSubmit = async (e) => {
          e.preventDefault();

        //       
        if (!idValid) {
          alert("  .    .");
          return;
        }

        //    
        if (idDuplicate) {
          alert("   .   .");
          return;
        }

        //   
        if (formData.pw !== formData.pw2) {
          alert("  .");
          return;
        }

        //   
        if (!pwValid) {
          alert("  .    .");
          return;
        }

        //   
        if (!nameValid) {
          alert("  .");
          return;
        }

        //   
        if (formData.nick && nickDuplicate) {
          alert("  .");
          return;
        }

        if (formData.nick && !validNick(formData.nick)) {
            alert("  .");
            return;
        }

        //   
        if (!emailValid) {
          alert("   .");
          return;
        }

        //   
        if (!phoneNumValid) {
          alert("    . xxx-xxxx-xxxx  .");
          return;
        }

        //   
        if (!addrValid) {
          alert(" , , , '-' ,  8  .");
          return;
        }

      
        //     (pw2 )
        const dataToSend = {
        id: formData.id,
        pw: formData.pw,
        name: formData.name,
        nick: formData.nick,
        email: formData.email,
        phone_num: formData.phone_num,
        addr: formData.addr,
        birth_date: formData.birth_date,
        };
    
        try {
          const response = await memJoin(dataToSend);
          if (response.data.success) {
            alert(" !");
            navigate(`/joinFin?name=${encodeURIComponent(formData.name)}`); //       
  
          } else {
            alert(" : " + response.data.message);
          }
        } catch (error) {
          alert("   .");
        }
      };

    return(
      <section>
      <div className={styles.join_base_section}>
        <div className={styles.join_base_wrap}>
          <div className={styles.filterHeader}>
            <h2></h2>
          </div>
          <br/>

          {/* Tabs Navigation */}
          <div className={styles.tabs}>
              <div className={styles.tab}></div>
              <div className={`${styles.tab} ${styles.active}`}></div>
              <div className={styles.tab}></div>
          </div>
          <br />
          <form onSubmit={handleSubmit}>
          <table className={styles.table}>
            <tr className={styles.tableRow}>
              <td className={styles.inputHead}>
                <i
                    className="fa-solid fa-square fa-2xs esse"
                    style={{
                        color: "#F294B2",
                        fontSize: "6px",
                        position: "relative",
                        bottom: "2px",
                        marginRight: "11px",
                    }}
                ></i>
                
                </td>
              <td>
                <input type="text" id="id" name="id" className={styles.textInput} oninput="validateInput(this)" value={formData.id} onChange={handleChange}/>
                <p className={`${styles.idMessage} ${idValid && !idDuplicate ? styles.success : styles.error}`}>
                  {idMsg}
                </p>
              </td>
            </tr>
            <tr className={styles.tableRow}>
              <td className={styles.inputHead}>
                <i
                    className={`fa-solid fa-square fa-2xs ${styles.esse}`}
                    style={{
                        color: "#F294B2",
                        fontSize: "6px",
                        position: "relative",
                        bottom: "2px",
                        marginRight: "11px",
                    }}
                ></i>
                </td>
              <td>
                <input type="password" name="pw" id="pw" className={styles.textInput} value={formData.pw} onChange={handleChange}/>
                <p className={`${styles.pwMessage} ${pwValid ? styles.success : styles.error}`}>
                  {pwMsg}
                </p>
              </td>
            </tr>
            <tr className={styles.tableRow}> 
              <td className={styles.inputHead}>
                <i
                    className={`fa-solid fa-square fa-2xs ${styles.esse}`}
                    style={{
                        color: "#F294B2",
                        fontSize: "6px",
                        position: "relative",
                        bottom: "2px",
                        marginRight: "11px",
                    }}
                ></i>
                 
              </td>
              <td>
                <input type="password" name="pw2" id="pw2" className={styles.textInput} value={formData.pw2} onChange={handleChange}/>
                {pwMatchMsg && (<p className={formData.pw === formData.pw2 ? styles.success : styles.error}>
                  {pwMatchMsg}
                </p>
                )}
              </td>
            </tr>
            <tr className={styles.tableRow}>
              <td className={styles.inputHead}>
                <i
                    className={`fa-solid fa-square fa-2xs ${styles.esse}`}
                    style={{
                        color: "#F294B2",
                        fontSize: "6px",
                        position: "relative",
                        bottom: "2px",
                        marginRight: "11px",
                    }}
                ></i></td>
              <td>
                <input type="text" name="name" id="name" className={styles.textInput} 
                oninput="validateInputK(this)" value={formData.name} onChange={handleChange}/>
              </td>
            </tr>
            <tr>
              <td className={styles.inputHead}></td>
              <td>
                <input type="text" name="nick" id="nick" className={styles.textInput} value={formData.nick} 
                  onBlur={() => checkNickDuplicate(formData.nick)}onChange={handleChange}/>
                   <p className={nickDuplicate ? styles.error : styles.success}>
                    {nickMsg}  {/*      */}
                  </p>
              </td>
            </tr>
            <tr>
              <td className={styles.inputHead}>
                <i
                    className={`fa-solid fa-square fa-2xs ${styles.esse}`}
                    style={{
                        color: "#F294B2",
                        fontSize: "6px",
                        position: "relative",
                        bottom: "2px",
                        marginRight: "11px",
                    }}
                ></i></td>
              <td>
              <div className={styles.emailContainer}>
                <label className={styles.checkboxLabel}>
                <input
                    type="text"
                    name="email"
                    placeholder=" "
                    value={formData.email}
                    onChange={handleChange}
                  />
                  <select
                    name="emailDomain"
                    onChange={handleChange}
                  >
                    <option value=""> </option>
                    <option value="naver.com">naver.com</option>
                    <option value="gmail.com">gmail.com</option>
                    <option value="daum.net">daum.net</option>
                    <option value="nate.com">nate.com</option>
                    <option value="hotmail.com">hotmail.com</option>
                  </select>
                </label>
                <p className={emailValid ? styles.success : styles.error}>{emailMsg}</p>
                </div>
                <p>
                    <i
                     className={`fa-solid fa-square fa-2xs ${styles.esse}`}
                        style={{
                            color: "#F294B2",
                            fontSize: "6px",
                            position: "relative",
                            bottom: "2px",
                            marginRight: "11px",
                        }}
                    ></i>
                  /    
                </p>
                <label className={styles.checkboxLabel}>
                  <p><input type="checkbox" id="maillingFllabel" /> /   .</p>
                </label>
              </td>
            </tr>
            <tr>
              <td className={styles.inputHead}><i
                    className={`fa-solid fa-square fa-2xs ${styles.esse}`}
                    style={{
                        color: "#F294B2",
                        fontSize: "6px",
                        position: "relative",
                        bottom: "2px",
                        marginRight: "11px",
                    }}
                ></i></td>
              <td>
                <input
                  type="text"
                  name="phone_num"
                  id="phone_num"
                  className={styles.textInput}
                  oninput="validateInputN(this)" 
                  placeholder=" -  "
                  value={formData.phone_num}
                  onChange={handleChange}
                />
                <p>
                <i
                    className={`fa-solid fa-square fa-2xs ${styles.esse}`}
                    style={{
                        color: "#F294B2",
                        fontSize: "6px",
                        position: "relative",
                        bottom: "2px",
                        marginRight: "11px",
                    }}
                ></i>
                           
                     .
                </p>
                <label className={styles.checkboxLabel}>
                  <p><input type="checkbox" id="smsFllabel" /> / SMS  .</p>
                </label>
              </td>
            </tr>
            <tr>
              <td className={styles.inputHead}
                ><i
                    className={`fa-solid fa-square fa-2xs ${styles.esse}`}
                    style={{
                        color: "#F294B2",
                        fontSize: "6px",
                        position: "relative",
                        bottom: "2px",
                        marginRight: "11px",
                    }}
                ></i></td>
              <td>
                <input type="text" name="addr" id="addr" className={styles.textInput} oninput="validateInputA(this)" value={formData.addr} onChange={handleChange}/>
              </td>
            </tr>
            <tr>
              <td className={styles.inputHead}>
                </td>
              <td>
                <label className={styles.checkboxLabel}>
                  <div className="datepicker-container">
                    <input type="date" id="birth_date" name="birth_date" value={formData.birth} onChange={handleChange}/>
                  </div>
                </label>
              </td>
            </tr>
          </table>
          <br />
          <div className={styles.buttonBox}>
          <Link to="/">
            <button id="btnCancel" className={styles.btn_member_cancel}></button>
          </Link>
            <button id="submit" className={styles.btn_comfirm}></button>
          </div>
          </form>
        </div>
      </div>
    </section>
    )
}

export default Join;